// /components/AuthProvider.js
'use client';

import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useApolloClient, gql } from '@apollo/client';

// Type definitions
interface AuthUser {
  id: string;
  email: string;
  role: string;
  name?: string;
  initials?: string;
}

interface AuthContextType {
  user: AuthUser | null;
  loading: boolean;
  login: (token: string, user: AuthUser) => void;
  logout: () => void;
}

// Create context
const AuthContext = createContext<AuthContextType | null>(null);

// Auth provider component
export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<AuthUser | null>(null);
  const [loading, setLoading] = useState(true);
  const client = useApolloClient();

  // Write user data to Apollo cache
  const updateUserCache = (userData: AuthUser | null) => {
    client.writeQuery({
      query: gql`
        query GetUser {
          user @client {
            id
            email
            role
            name
            initials
          }
        }
      `,
      data: {
        user: userData
      }
    });
  };

  // Parse JWT token to get initials
  const parseUserFromToken = (token: string): { id: string; email: string; role: string } | null => {
    if (!token) return null;
    
    try {
      // JWT tokens are in format: header.payload.signature
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map(c => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
          .join('')
      );
      
      return JSON.parse(jsonPayload);
    } catch (error) {
      console.error('Error parsing JWT token:', error);
      return null;
    }
  };

  // Format initials from name or email
  const getInitials = (name?: string, email?: string): string => {
    if (name) {
      // Get first letter of each word in name
      return name
        .split(' ')
        .map(part => part.charAt(0))
        .join('')
        .toUpperCase()
        .substring(0, 2);
    } else if (email) {
      // Use first letter of email
      return email.charAt(0).toUpperCase();
    }
    return 'U'; // Default
  };

  // Login function - called after successful authentication
  const login = (token: string, userData: AuthUser) => {
    // Store token in localStorage
    localStorage.setItem('auth_token', token);
    
    // Extract user data from token if not all fields are provided
    const tokenData = parseUserFromToken(token);
    
    // Create complete user object
    const user: AuthUser = {
      ...userData,
      id: userData.id || tokenData?.id || '',
      email: userData.email || tokenData?.email || '',
      role: userData.role || tokenData?.role || 'default',
    };
    
    // Add initials if not provided
    if (!user.initials) {
      user.initials = getInitials(user.name, user.email);
    }
    
    // Update state
    setUser(user);
    
    // Update Apollo cache
    updateUserCache(user);
  };

  // Logout function
  const logout = () => {
    localStorage.removeItem('auth_token');
    setUser(null);
    updateUserCache(null);
    
    // Clear Apollo cache
    client.resetStore();
  };

  // Check for existing token on mount
  useEffect(() => {
    const checkAuth = async () => {
      const token = localStorage.getItem('auth_token');
      
      if (token) {
        try {
          // Parse user data from token
          const tokenData = parseUserFromToken(token);
          
          if (tokenData) {
            // Create user object
            const user: AuthUser = {
              id: tokenData.id,
              email: tokenData.email,
              role: tokenData.role,
              initials: getInitials(undefined, tokenData.email)
            };
            
            // Update state
            setUser(user);
            
            // Update Apollo cache
            updateUserCache(user);
          }
        } catch (error) {
          console.error('Error restoring auth state:', error);
          localStorage.removeItem('auth_token');
        }
      }
      
      setLoading(false);
    };
    
    checkAuth();
  }, []);

  return (
    <AuthContext.Provider value={{ user, loading, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
}

// Custom hook for using auth context
export function useAuth() {
  const context = useContext(AuthContext);
  
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  
  return context;
}